import common from'../../util/common.js'
import code from "../../util/code.js"
import qs from"../../components/qs.js"
export default {
	data() {
		return {
			noticeMark:'',//公告
			startTime:'',//开始时间
			endTime:'',//结束时间
			duration:'',//时长
			timeMode:'',//1任意时间 2 区县时间 3 固定时间
			nextV:'' ,//考试状态
			radioNum:'0',//单选题数量
			radioScore:'0',//单选题分数
			checkNum:'0',//多选题数量
			checkScore:'0',//多选题分数
			judgeNum:'0',//判断数量
			judgeScore:'0',//判断题分数
			fillNum:'0',//填空题数量
			fillScore:'0',//填空题分数
			subNum:'0',//主观题数量
			subScore:'0',//主观题分数
			sumScore:'0',//总分

		};

	},
	onLoad() {
		var userId = uni.getStorageSync("userIdS");
		var examId = uni.getStorageSync("examIdS");
		var markV =  uni.getStorageSync("markS")
		if (userId == "" || userId == undefined || userId == null || userId == NaN || userId == 'NaN'||
			examId == "" || examId == undefined || examId == null || examId == NaN || examId == 'NaN' ||
			markV == "" || markV == undefined || markV == null || markV == NaN || markV == 'NaN') {
			uni.reLaunch({
				url:'../index/index?mark =' + markV
			})
			console.log("执行了");
			return
		}else{
			uni.request({
				url: common.port.mMark,
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				method: "POST",
				dataType: "json",
				data: {
					mark: markV
				},
				success: (result) => {
					console.log(result)
					if (result.data.code == 0) {
						this.noticeMark = result.data.data.remark;//公告
						this.radioNum = result.data.data.radio_num;//单选题数量
						this.radioScore = result.data.data.radio_score;//单选题分数
						this.checkNum = result.data.data.check_num;//多选题数量
						this.checkScore = result.data.data.check_score;//多选题分数
						this.judgeNum = result.data.data.judge_num;//判断数量
						this.judgeScore = result.data.data.judge_score;//判断题分数
						this.fillNum = result.data.data.fill_num;//填空题数量
						this.fillScore = result.data.data.fill_score;//填空题分数
						this.subNum = result.data.data.sub_num;//主观题数量
						this.subScore = result.data.data.sub_score;//主观题分数
						this.sumScore = this.radioNum * this.radioScore +
						this.checkNum*this.checkScore+this.judgeNum*this.judgeScore+
						this.fillNum*this.fillScore+this.subNum*this.subScore;
						this.timeMode = uni.getStorageSync("timeModeS");
						this.startTime = uni.getStorageSync("startS");
						this.endTime = uni.getStorageSync("endS");
						this.duration = uni.getStorageSync("durationS");
						if(result.data.data.join_info.length > 2){
							uni.setStorageSync("joinInfoS",result.data.data.join_info)
						}else{
							uni.setStorageSync("joinInfoS",0)
						}

						var nowTime = code.CurTime();//现在时间
						// 判断按钮状态
						if(this.timeMode == 1){//任意时间
							// 判断是否报名
							uni.request({
								url: common.port.mIsJoin,
								header: {
									'content-type': 'application/x-www-form-urlencoded',
								},
								method: "POST",
								dataType: "json",
								data: {
									exam_id: examId,
									user_id: userId
								},
								success: (result) => {
									if(result.data.code == 500){
										uni.showToast({
											title: result.msg,
											icon: 'none',
											duration: 2000
										})
									}else{
										if(result.data.is_join == false){//未报名]
												console.log(uni.getStorageSync("joinInfoS"));
												if(uni.getStorageSync("joinInfoS") == 0){//不需要填写报名信息
													this.nextV = 1
												}else{
													this.nextV = 0
												}
											}else{
												//已报名
												this.nextV = 1
											}
									}
								},
							})
						}else if(this.timeMode == 2){//区间时间
							if(nowTime >= code.DateToUnix(this.endTime)){
								// 1 考试结束
								this.nextV = 3
							}if(nowTime < code.DateToUnix(this.startTime)){
								// 1 考试未结束
								this.nextV = 2
							}else{
								//考试进行中// 判断是否报名
								uni.request({
									url: common.port.mIsJoin,
									header: {
										'content-type': 'application/x-www-form-urlencoded',
									},
									method: "POST",
									dataType: "json",
									data: {
										exam_id: examId,
										user_id: userId
									},
									success: (result) => {
										if(result.data.code == 500){
											uni.showToast({
												title: result.msg,
												icon: 'none',
												duration: 2000
											})
										}else{
											if(result.data.is_join == false){
													console.log(uni.getStorageSync("joinInfoS"));
													if(uni.getStorageSync("joinInfoS") == 0){//不需要填写报名信息
														this.nextV = 1
													}else{
														this.nextV = 0
													}
												}else{
													//已报名
													this.nextV = 1
												}
										}
									},
								})
							}
						}else if(this.timeMode== 3){//固定时间
							if(nowTime > (code.DateToUnix(this.startTime) + 18000)){
								// 考试结束
								this.nextV = 3
							}else if(nowTime < code.DateToUnix(this.startTime)){
								//考试未开始
								this.nextV = 2
							}else{
								//考试进行中// 判断是否报名
								uni.request({
									url: common.port.mIsJoin,
									header: {
										'content-type': 'application/x-www-form-urlencoded',
									},
									method: "POST",
									dataType: "json",
									data: {
										exam_id: examId,
										user_id: userId
									},
									success: (result) => {
										if(result.data.code == 500){
											uni.showToast({
												title: result.msg,
												icon: 'none',
												duration: 2000
											})
										}else{
											if(result.data.is_join == false){
													if(uni.getStorageSync("joinInfoS") == 0){//不需要填写报名信息
														this.nextV = 1
													}else{
														this.nextV = 0
													}
												}else{
													//已报名
													this.nextV = 1
												}
										}
									},
								})
							}
						}
					}else{
						uni.showToast({
							title: result.msg,
							icon: 'none',
							duration: 2000,
							success:function(){
								uni.navigateTo({
									url:'../index/index'
								})
							}
						})
					}
				},
			})
		}
	},
	methods: {
		// 下一步
		nextBtn:function(e){
			uni.navigateTo({
				url:'../sign/sign'
			})
		},
		//开始开始
		startBtn:function(){
			uni.navigateTo({
				url:"../simulate/simulate"
			})
		},
	}
};
